.navigation {
    .icon {
        display: inline-block;
        vertical-align: middle;
        font-size: 12px; }
    &__title {
        vertical-align: middle;
        display: inline-block;
        font-family: $montserrat;
        font-size: 12px;
        font-weight: bold;
        text-transform: uppercase;
        line-height: 1.75;
        color: $dark;
        margin-left: 10px; }
    &__logo {
        @include sm {
            padding: 14px 0;
 } }            // text-align: center
    &_theme {
        .navigation__list {
            justify-self: center;
            margin: 0 -44px 0 0;
            @include sm {
                margin: 0; } }
        .navigation__link {
            padding: 33px 18px;
            @include sm {
                padding: 18px 0; } }
        .navigation__btn {
            font-weight: 400;
            @include sm {
                margin-top: 10px; } }
        @include sm {
            border-top: 1px solid rgba(#979797, 0.3); } } }





