.what {
    padding: 90px 0 92px;
    border-bottom: 1px solid rgba(#979797, 0.3);
    @include sm {
        padding: 60px 0; }
    &__title {
        text-align: center; }
    &__descr {
        padding: 0 235px;
        margin-bottom: 67px;
        margin-top: 7px;
        @include md {
            padding: 0 100px; }
        @include sm {
            padding: 0; } }
    &__row {
        display: flex;
        justify-content: center;
        margin: 0 -20px;
        padding: 0 116px;
        @include lg {
            padding: 0;
            max-width: 970px;
            margin: 0 auto; }
        @include md {
            padding: 0;
            margin: 0 -8px; }
        @include sm {
            flex-direction: column;
            // align-items: center
            padding: 0; } }
    .card {
        padding: 0 20px;
        flex: 0 0 33.333%;
        @include lg {
            padding: 0 15px; }
        @include md {
            padding: 0 8px; }
        @include sm {
            flex: 0 0 100%;
            padding-bottom: 20px; } } }

.card {
    &__img-wrapper {
        position: relative;
        max-width: 287px;
        height: 271px;
        background-color: $white;
        box-shadow: 0 33px 71px 0 rgba(63, 86, 110, 0.1), 0 0 24px 0 rgba(99, 246, 235, 0.04);
        font-size: 0;
        @include lg {
            @include vertical; }
        @include sm {
            max-width: 100%; } }
    &__image {
        position: absolute;
        @include lg {
            position: static;
            display: inline-block;
            vertical-align: middle;
            max-width: 100%; }
        .card_eset & {
            margin-top: 20px;
            margin-left: -40px;
            @include lg {
                margin: 0; } } }

    &__brand {
        margin-top: 26px;
        min-height: 30px;
        text-align: center;
        position: relative;
        .card_sinae & {
            margin-top: 21px; }
        @include sm {
            margin-top: 10px; }
        img {
            @include vcenter; } } }
