.who {
    padding: 109px 0 0;
    @include md {
        padding-top: 60px; }
    @include sm {
        padding-top: 40px; }
    &__title {
        text-align: center; }
    &__subtitle {
        text-align: center;
        margin-top: 7px; }
    &__content {
        padding: 0 88px 0 142px;
        display: flex;
        justify-content: space-between;
        margin-top: 59px;
        @include lg {
            padding: 0;
            max-width: 940px;
            margin: 59px auto 0; }
        @include sm {
            flex-direction: column;
            margin-top: 30px; } }
    &__item {
        &:not(:first-child) {
            margin-left: 15px;
            @include sm {
                margin-left: 0;
                margin-top: 15px; } } } }

.who-item {
    padding: 40px 33px 34px 34px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 287px;
    box-shadow: 0 33px 71px 0 rgba(63, 86, 110, 0.1), 0 0 24px 0 rgba(99, 246, 235, 0.04);
    background-color: $white;
    @include sm {
        max-width: none; }
    &__img {
        max-width: 100%;
        min-height: 110px;
        display: flex;
        align-items: center; }
    &__title {
        margin-top: 25px;
        font-family: $montserrat;
        text-transform: uppercase;
        font-size: 14px;
        letter-spacing: 0.4px;
        font-weight: 600; }
    &__text {
        margin-top: 2px;
        color: rgba($dark,.7); } }
