.how__item { padding: 21px 10px; flex: 0 0 50%; max-width: 50%; }

@media only screen and (max-width: 599px) { .how__item { flex: 0 0 100%; padding: 10px; max-width: 100%; } }

.how-item { display: flex; flex-direction: column; align-items: center; }

.how-item__img { position: relative; width: 54px; height: 54px; border-radius: 50%; display: flex; align-items: center; justify-content: center; color: #303a44; }

.how-item__img .icon { display: block; font-size: 24px; fill: none; stroke: currentColor; }

.how-item__number { position: absolute; left: 3px; top: 0; line-height: 1.3; }

.how-item__wrap { max-width: 220px; text-align: center; font-size: 14px; margin-top: 13px; }

.how-item__title { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-weight: 600; letter-spacing: 0.4px; }

.how-item__text { opacity: 0.7; margin-top: 2px; }
